import request from '@/utils/request'
// 获取小程序数据
export function getWechatMiniInfo(id) {
  return request({
    url: `/wechat_mini/businessMini/getWechatMiniInfo?shopId=${id}`,
    method: 'get'
  })
}
// 刷新小程序信息
export function refreshMiniInfo(id) {
  return request({
    url: `/wechat_mini/businessMini/refreshMiniInfo?shopId=${id}`,
    method: 'get'
  })
}
// 获取最新小程序代码信息
export function getLastCodeInfo(id) {
  return request({
    url: `/wechat_mini/businessMini/getLastCodeInfo`,
    method: 'get'
  })
}
// 商家小程序上传小程序代码
export function addBusinessMiniCode(obj) {
  return request({
    url: `/wechat_mini/api/business-code/addBusinessMiniCode?shopId=${obj.shopId}&templateId=${obj.templateId}&type=${obj.type}`,
    method: 'get'
  })
}
// 商家小程序上传小程序代码
export function getCodeHistory(t) {
  return request({
    url: `/wechat_mini/businessMini/getCodeHistory?type=${t}`,
    method: 'get'
  })
}
