import request from '@/utils/request'
// 获取扫码地址
export function getAuthUrl(id,num) {
  return request({
    url: `/wechat_mp/auth/getAuthUrl?shopId=${id}&type=${num}`,
    method: 'get',
  })
}
// 
export function findShopMpAuth(id,type) {
  return request({
    url: `/sys/businessWechat/findShopMpAuth?shopId=${id}&wechatType=${type}`,
    method: 'get',
  })
}

