<template>
  <div class="pageContent" v-loading="loading" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading">
    <!-- 未授权状态下 -->
    <div v-if="boxType">
      <el-card style="padding:40px 0;margin-top:20px;">
        <div class="head">将微信小程序绑定店铺,提升交易量</div>
        <div class="flex-row-around">
          <el-card class="conBox" shadow="hover">
            <div class="flex-row-center">
              <img class="bindimg" src="../../../assets/images/wechat/bindWechat.png" alt="">
            </div>
            <div class="flex-row-center mt20">
              <el-button type="primary" @click="bindMp">已有小程序，立即绑定</el-button>
            </div>
            <div class="tac mt20">为保证所有功能的正常使用</div>
            <div class="tac">授权时请把所有权限同意授权给筷赚</div>
          </el-card>
          <el-card class="conBox" shadow="hover">
            <div class="flex-row-center">
              <img class="bindimg" src="../../../assets/images/wechat/newWechat.png" alt="">
            </div>
            <div class="flex-row-center mt20">
              <el-button type="primary" plain @click="openPage('https://mp.weixin.qq.com/')">没有小程序，立即申请</el-button>
            </div>
            <div class="tac mt20">请在微信公众平台申请新的公众号</div>
            <div class="tac">成功后在到筷赚进行绑定</div>
          </el-card>
        </div>
      </el-card>
      <!-- 帮助中心 -->
      <el-card shadow="hover" style="margin-top:20px;">
        <div class="flex-row-around">
          <div class="li flex-row-start">
            <img src="../../../assets/images/wechat/help1.png" alt="">
            <el-button class="btn" type="text">小程序绑定教程</el-button>
          </div>
          <div class="li flex-row-start">
            <img src="../../../assets/images/wechat/help2.png" alt="">
            <el-button class="btn" type="text">小程序绑定注意事项</el-button>
          </div>
          <div class="li flex-row-start">
            <img src="../../../assets/images/wechat/help3.png" alt="">
            <el-button class="btn" type="text">不同小程序类型的功能区别</el-button>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 已授权状态下 -->
    <el-card v-else>
      <div class="flex-row-between">
        <div class="flex-row-start">
          <div class="shopName">{{form.miniInfo.name}}</div>
          <el-tag type="danger" v-if="form.miniInfo.verifyTypeInfo == 0">未认证</el-tag>
          <el-tag type="success" v-if="form.miniInfo.verifyTypeInfo == 1">已认证</el-tag>
        </div>
        <div>
          <el-button type="text" icon="el-icon-refresh" @click="reFresh">刷新</el-button>
          <el-tooltip class="item" effect="dark" content="更新小程序基本信息后，可能筷赚与微信数据同步不及时，点击刷新获取最新数据" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
      </div>
      <el-divider></el-divider>
      <div class="pageHead">基本信息</div>
      <div class="flex-row-between">
        <div>
          <el-form ref="form" :model="form" label-width="140px">
            <el-form-item label="Appid:">
              <div>
                <span>{{form.miniInfo.appId}}</span>
                <el-button class="cobyOrderSn" :data-clipboard-text="form.miniInfo.appId" @click="copyLink" icon="el-icon-document-copy" type="text"></el-button>
              </div>
            </el-form-item>
            <el-form-item label="类目:">
              <div v-for="(item,index) in form.miniInfo.categoryArr" :key="index">{{item}}</div>
            </el-form-item>
            <el-form-item label="介绍:">
              <div>{{form.miniInfo.signature}}</div>
            </el-form-item>
          </el-form>
        </div>
        <div class="flex-column-center">
          <div>
            <el-image :src="form.miniInfo.qrcodeUrl" style="width: 150px; height: 150px"  fit="fill"></el-image>
          </div>
          <div>
            <el-link :href="form.miniInfo.qrcodeUrl" target="_blank">下载</el-link>
          </div>
        </div>
      </div>
      <div class="pageHead" v-if="form.nowCodeInfo">线上版本</div>
      <el-form ref="form" v-if="form.nowCodeInfo" label-width="140px">
        <el-form-item label="当前版本:">
          <div class="flex-row-start">
            <div>{{form.nowCodeInfo.codeVersion}}</div>
            <div style="margin:0 20px;"><el-tag type="success">已发布</el-tag></div>
          </div>
        </el-form-item>
        <el-form-item label="版本描述:">
          <div>{{form.nowCodeInfo.codeDesc}}</div>
        </el-form-item>
        <el-form-item label="发布时间:">
          <div>{{form.nowCodeInfo.createTime}}</div>
        </el-form-item>
      </el-form>
      <!-- 审核中  -->
      <div class="pageHead" v-if="form.applyCodeInfo">版本审核</div>
      <el-form ref="form" v-if="form.applyCodeInfo" label-width="140px">
        <el-form-item label="审核版本:">
          <div class="flex-row-start">
            <div>{{form.applyCodeInfo.codeVersion}}</div>
            <div style="margin:0 20px;" v-if="form.applyCodeInfo.status == 0"><el-tag type="warning">审核中</el-tag></div>
            <div style="color:#E6A23C;" v-if="form.applyCodeInfo.status == 0">预计审核时间1-3个工作日</div>
            <div style="margin:0 20px;" v-if="form.applyCodeInfo.status == 2"><el-tag type="danger">审核不通过</el-tag></div>
            <div style="color:#F56C6C;" v-if="form.applyCodeInfo.status == 2">审核不通过原因：{{form.applyCodeInfo.reason}}</div>
          </div>
        </el-form-item>
        <el-form-item label="版本描述:">
          <div>{{form.applyCodeInfo.codeDesc}}</div>
        </el-form-item>
        <el-form-item label="审核时间:">
          <div>{{form.applyCodeInfo.createTime}}</div>
        </el-form-item>
        <el-form-item v-if="form.applyCodeInfo.status == 2">
          <el-button type="primary" @click="selectEdition">重新发布</el-button>
        </el-form-item>
      </el-form>
      <!-- 未发布版本 -->
      <div v-if="!form.applyCodeInfo && !form.nowCodeInfo">
        <el-divider></el-divider>
        <div style="padding-left:100px;">
          <el-button @click="selectEdition">选择功能版本</el-button>
        </div>
        <div style="padding-left:100px;margin-top:40px;color:#909399;">根据您的需要选择合适的小程序功能版本,发布后等待微信审核,通过后即可生成店铺小程序二维码</div>
      </div>
      <el-divider></el-divider>
      <div class="flex-row-start">
        <div class="btns flex-row-start" @click="reAuth">
          <img class="btnImg" src="../../../assets/images/wechat/btns2.png" alt="">
          <span class="btnSpan">重新授权</span>
        </div>
        <div class="btns flex-row-start" @click="showListFun">
          <img class="btnImg" src="../../../assets/images/wechat/btns3.png" alt="">
          <span class="btnSpan">已授权列表</span>
        </div>
      </div>
    </el-card>
    <!-- 授权结果弹框 -->
    <el-dialog title="授权结果" :visible.sync="showDia" width="30%" center>
      <span slot="footer" class="dialog-footer">
        <el-button @click="$router.go(0)">授权失败</el-button>
        <el-button type="primary" @click="$router.go(0)">授权成功</el-button>
      </span>
    </el-dialog>
    <!-- 授权列表弹框 -->
    <el-dialog title="授权列表" :visible.sync="showList" width="70%">
      <div class="flex-row-between flex-wrap">
        <div class="liItem flex-row-start" v-for="(item,index) in list" :key="index">
          <i style="color:#409EFF;" v-if="item.selected == 1" class="el-icon-circle-check"></i>
          <i style="color:#F56C6C;" v-if="item.selected == 0" class="el-icon-circle-close"></i>
          <span style="margin-left:10px;">{{item.authName}}</span>
        </div>
      </div>
    </el-dialog>
    <!-- 未发布 - 选择版本弹框 -->
    <el-dialog title="选择小程序功能" :visible.sync="showSelectEdition" width="70%">
      <div class="selectEdition">
        <div class="pageHead">绑定说明</div>
        <div class="fontBox">
          <p class="fontItem">
            1、一个小程序仅能选择发布一个版本，多个小程序可发布同一个版本；版本发布后不可更换
          </p>
          <p class="fontItem">
            2、版本可升级时，系统将自动提交小程序代码给微信审核，个别清空需要商户手动出发升级；
          </p>
          <p class="fontItem">
            3、请根据功能建议的小程序类型及类目进行选择版本，否则可能导致小程序功能无法正常使用，或者代码在微信审核失败。
          </p>
        </div>
        <div class="listBox">
          <div class="listTitle">请选择小城需要绑定的功能：</div>
          <div class="listBoxs flex-row-around flex-wrap">
            <div class="listBoxCard" @click="selectEdi(item)" v-for="(item,index) in selectEditionList" :key="index">
              <el-card shadow="hover" :class="item.active ? 'activeCard' : ''">
                <el-form ref="form" :model="form" label-width="120px">
                  <div style="margin-bottom:22px;" class="flex-row-between">
                    <el-form-item v-if="item.type == 0" style="margin-bottom:0;" label="微商城"></el-form-item>
                    <el-form-item v-if="item.type == 1" style="margin-bottom:0;" label="其他"></el-form-item>
                    <el-button type="text" @click.stop="historyEdi(item)">历史版本</el-button>
                  </div>
                  <el-form-item label="当前版本号：">
                    <div>{{item.codeVersion}}</div>
                  </el-form-item>
                  <el-form-item label="版本介绍：">
                    <div>{{item.codeDesc}}</div>
                  </el-form-item>
                </el-form>
              </el-card>
            </div>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showSelectEdition = false">取 消</el-button>
        <el-button :loading="selectEditionLoading" type="primary" @click="selectEditionFun">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getAuthUrl , findShopMpAuth } from '@/api/weChat/mpManage'
import { 
  getWechatMiniInfo,
  refreshMiniInfo,
  getLastCodeInfo,
  addBusinessMiniCode
} from '@/api/weChat/miniManage'

export default {
  data(){
    return{
      page:'',                            // 授权链接
      form:{
        miniInfo:{},
      },
      boxType:false,                      // 是否授权
      loading:true,                       // 全局loading
      showDia:false,                      // 授权结果弹出框
      showList:false,                     // 授权列表弹框
      list:[],                            // 授权列表数据
      showSelectEdition:false,            // 未发布 - 选择版本弹框
      selectEditionList:[],               // 版本数据
      selectEdiItem:{},                   // 选中的版本
      selectEditionLoading:false,         // 发布版本loading
    }
  },
  created(){
    this.getData();
    this.getLink();
  },
  methods:{
    // 未发布 - 选择版本
    selectEdition(){
      if(!this.form.miniInfo.name || !this.form.miniInfo.category ){
        this.$message.error('请到微信小程序后台完善基本信息设置');
        return;
      }
      this.getEdition();
      this.showSelectEdition = true;
    },
    selectEdi(item){
      console.log('点击了')
      this.selectEditionList.forEach(e=>e.active = false)
      item.active = true;
      this.selectEdiItem = item;
    },
    // 未发布 - 获取功能版本
    getEdition(){
      getLastCodeInfo().then(res=>{
        console.log('功能版本',res)
        if(res.code == 200){
          res.data.forEach(e=>e.active = false)
          this.selectEditionList = res.data
        }
      })
    },
    selectEditionFun(){
      if(!this.selectEdiItem){
        this.$message.error('请选择要发布的版本');
        return;
      }
      this.selectEditionLoading = true;
      let obj = {
        shopId:this.shop.shopId,
        templateId:this.selectEdiItem.templateId,
        type:this.selectEdiItem.type
      }
      addBusinessMiniCode(obj).then(res=>{
        console.log('发布版本',res)
        if(res.code == 200){
          this.$message({
            showClose: true,
            message: '提交审核成功！微信会在1-3个工作日内进行审核，审核成功后，系统会自动发布小程序。',
            type: 'success'
          });
        }else{
          this.$message({
            showClose: true,
            message: '小程序提交审核失败请重试或联系客服',
            type: 'error'
          });
        }
        setTimeout(()=>{this.selectEditionLoading = false},1000)
        this.showSelectEdition = false;
        this.getData()
      })
    },
    historyEdi(e){
      console.log('点了历史')
      let details = this.$router.resolve({
        name: "历史版本",
      });
      window.open(details.href+'?type='+e.type, "_blank")
    },
    // 重新授权
    reAuth(){
      const h = this.$createElement;
      this.$msgbox({
        title: '请您注意',
        message: h('p', null, [
          h('div', null, '1.重新授权时不可换绑其他微信公众号,否则重新授权将失败。 '),
          h('div', null, '2.为保证您在筷赚平台功能的正常使用,授权时请保持默认选择,把开房平台账号管理权限统一授权给筷赚。 '),
        ]),
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
      }).then(action => {
        this.openPage(this.page)
        this.showDia = true
      }).catch(() => {});
    },
    // 点击授权列表
    showListFun(){
      this.getList();
      this.showList = true
    },
    // 获取授权列表
    getList(){
      findShopMpAuth(this.shop.shopId,1).then(res=>{
        console.log('授权列表',res)
        if(res.code == 200){
          this.list = res.data
        }
      })
    },
    // 获取授权地址
    getLink(){
      getAuthUrl(this.shop.shopId,2).then(res=>{
        console.log('小程序授权地址',res)
        this.page = res.data
      })
    },
    // 获取数据
    getData(){
      getWechatMiniInfo(this.shop.shopId).then(res=>{
        console.log('授权数据',res)
        if(res.data.miniInfo == null){
          console.log('未授权')
          this.boxType = true;
        }else{
          this.boxType = false;
          this.form = res.data;
          this.form.miniInfo.categoryArr = this.form.miniInfo.category.split(';')
          console.log('授权了')
        }
        this.loading = false;
      })
    },
    // 刷新 - 右上角
    reFresh(){
      this.loading = true;
      refreshMiniInfo(this.shop.shopId).then(res=>{
        console.log('刷新',res)
        setTimeout(()=>{
          this.getData()
        },1500)
      })
    },
    // 未授权 - 点击绑定
    bindMp(){
      this.showDia = true;
      this.openPage(this.page)
    },
    // 未授权 - 已有
    openPage(page){
      window.open(page, "_blank");
    },
    // appid - 复制链接
    copyLink() {
      let clipboard = new this.clipboard('.cobyOrderSn');
      console.log(clipboard)
      clipboard.on('success', () => {
        this.$message({
          message: '复制成功',
          type: 'success'
        });
      });
      clipboard.on('error', () => {
        this.$message.error('复制失败');
      });
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .liItem{
    width: 48%;
    line-height: 40px;
    font-size: 14px;
  }
  .selectEdition{
    .fontBox{
      width: 100%;
      .fontItem{
        margin-bottom: 10px;
        line-height: 24px;
        font-size: 14px;
      }
    }
    .listBox{
      .listTitle{
        font-size: 16px;
        color: #303133;
        margin: 40px 0;
      }
      .listBoxCard{
        width: 45%;
        cursor: pointer;
        .activeCard{
          border-color:#409EFF; 
        }
      }
    }
  }
  .shopName{
    font-size: 18px;
    font-weight: bold;
    margin-right: 20px;
  }
  .cobyOrderSn{
    margin-left: 10px;
  }
  .btns{
    cursor: pointer;
    margin-right: 50px;
    .btnImg{
      width: 40px;
      height: 40px;
    }
    .btnSpan{
      font-size: 14px;
      margin-left: 8px;
    }
    &:hover{
      .btnSpan{
        color: #409EFF;
      }
    }
  }
  .mt20{
    margin-top: 20px;
  }
  .tac{
    text-align: center;
    font-size: 14px;
    color: #909399;
  }
  .head{
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    color: #303133;
    padding-bottom: 60px;
  }
  .conBox{
    padding: 60px 0;
    width: 45%;
    .bindimg{
      width: 80px;
      height: 80px;
    }
  }
  .li{
    .btn{
      color:#303133;
      margin-left: 10px ;
      &:hover{
        color: #409EFF;
      }
    }
    img{
      width: 40px;
      height: 40px;
      vertical-align: top;
    }
  }
</style>